import React, { useState } from "react"
import { Button, Card } from "react-bootstrap"
import Truncate from "react-truncate-html"
import { useAuth } from "../../contexts/AuthContext"
import app from "../../firebase"
import defaultMoviePoster from "../../assets/default_movie_poster.png"

export default function MediaCard(props) {
  const { currentUser } = useAuth()
  const db = app.database().ref("users").child(`${currentUser.uid}/watchlist`)
  const [inWatchList, setInWatchList] = useState(
    props.result.id in props.watchList
  )
  const [render, setRender] = useState(true)

  return (
    render && (
      <div className="pb-4 col-6 col-sm-6 col-md-4 col-lg-3 col-xl-2">
        <Card className="border-0">
          <div
            className="border=0 embed-responsive"
            style={{ paddingBottom: "150%" }}
          >
            <Card.Img
              variant="top"
              src={
                props.result.poster_path
                  ? `https://image.tmdb.org/t/p/w342/${props.result.poster_path}`
                  : defaultMoviePoster
              }
              className="embed-responsive-item"
            />
          </div>

          <Card.Body className="p-0">
            <Card.Title className="mt-1 mb-0 p-0" style={{ fontSize: "14px" }}>
              <Truncate
                lines={2}
                portrait={2}
                dangerouslySetInnerHTML={{
                  __html:
                    props.result.original_title ||
                    props.result.original_name ||
                    "No title",
                }}
              />
            </Card.Title>

            <div className="d-flex">
              {`${
                props.result.release_date
                  ? props.result.release_date.substring(0, 4)
                  : props.result.first_air_date
                  ? props.result.first_air_date.substring(0, 4)
                  : "No Date"
              } • 
            ${props.result.media_type === "movie" ? "Movie" : "Show"}`}
            </div>
            <div className="d-flex justify-content-center">
              {inWatchList ? (
                <Button
                  id={props.result.id}
                  className="mt-1"
                  style={{
                    width: "100%",
                  }}
                  variant="primary"
                  onClick={async (e) => {
                    await db.update({ [e.target.id]: null })
                    if (props.deleteFully) {
                      setRender(false)
                    } else {
                      setInWatchList(false)
                    }
                  }}
                >
                  Remove
                </Button>
              ) : (
                <Button
                  id={props.result.id}
                  className="mt-1"
                  style={{
                    width: "100%",
                  }}
                  variant="primary"
                  onClick={async (e) => {
                    await db
                      .update({
                        [e.target.id]: {
                          type: props.result.media_type,
                          ID: e.target.id,
                        },
                      })
                      .then(setInWatchList(true))
                  }}
                >
                  Add
                </Button>
              )}
            </div>
          </Card.Body>
        </Card>
      </div>
    )
  )
}
