import React, { useState } from "react"
import { Form, Button, Container, InputGroup, Alert } from "react-bootstrap"
import "font-awesome/css/font-awesome.min.css"
import MediaCard from "./MediaCard.js"
import { useAuth } from "../../contexts/AuthContext"
import app from "../../firebase"
import tmdbLogo from "../../assets/tmdblogo.svg"

export default function Search() {
  const { currentUser } = useAuth()
  const [inputValue, setInputValue] = useState("")
  const [results, setResults] = useState([])
  const [error, setError] = useState("")
  const [loading, setLoading] = useState(false)
  const [watchList, setWatchList] = useState({})

  const searchDB = async () => {
    setLoading(true)
    await app
      .database()
      .ref("users")
      .child(`${currentUser.uid}/watchlist/`)
      .once("value", async (snapshot) => {
        snapshot.exists() && setWatchList(snapshot.val())
      })

    await fetch(
      `https://api.themoviedb.org/3/search/multi?api_key=${process.env.REACT_APP_TMDB_API_KEY}&language=en-US&query=${inputValue}&page=1&include_adult=false`
    )
      .then((res) => res.json())
      .then((data) => {
        data.total_results === 0 && setError("No Results Found")
        setResults(data.results)
      })
    setLoading(false)
  }

  return (
    <>
      <Container className="pt-4">
        <InputGroup className="mb-1">
          <Form.Control
            className="py-2 br-0 border input-lg"
            type="search"
            placeholder="Find movies and shows"
            value={inputValue}
            onChange={(e) => {
              setInputValue(e.target.value)
              setError("")
            }}
            onKeyPress={(e) => {
              if (e.charCode === 13) {
                searchDB()
              }
            }}
            style={{ fontSize: "16px" }}
          />
          <InputGroup.Append>
            <Button onClick={searchDB} disabled={loading}>
              {loading ? (
                <span
                  class="spinner-border spinner-border-sm"
                  role="status"
                  aria-hidden="true"
                ></span>
              ) : (
                "Search"
              )}
            </Button>
          </InputGroup.Append>
        </InputGroup>
        <div className="mt-2 mb-4" style={{ float: "right" }}>
          Powered by:
          <img
            className="ml-2"
            style={{ height: "10px" }}
            src={tmdbLogo}
            alt="TMDB Logo"
          />
        </div>

        {error && <Alert variant="dark">{error}</Alert>}
        <div className="row">
          {results
            ? results.map((result) => {
                return (
                  <MediaCard
                    watchList={watchList}
                    key={result.id}
                    result={result}
                  />
                )
              })
            : null}
        </div>
      </Container>
    </>
  )
}
