import React, { useState, useEffect } from "react"
import { useAuth } from "../contexts/AuthContext"
import app from "../firebase"
import NavigationMenu from "./page-elements/NavigationMenu.js"
import MediaCard from "./page-elements/MediaCard.js"
import { Container } from "react-bootstrap"

/* Convert this dashboard to a functional one where users can search for movies and shows */

export default function WatchList() {
  const { currentUser } = useAuth()
  const [results, setResults] = useState([])
  const [watchList, setWatchList] = useState({})

  async function fetchData(snapshot) {
    await app
      .database()
      .ref("users")
      .child(`${currentUser.uid}/watchlist/`)
      .once("value", async (snapshot) => {
        snapshot.exists() && setWatchList(snapshot.val())
      })
    Object.keys(snapshot.val()).map((key) => {
      if (snapshot.val()[key].type === "movie") {
        fetch(
          `https://api.themoviedb.org/3/movie/${
            snapshot.val()[key].ID
          }?api_key=${process.env.REACT_APP_TMDB_API_KEY}&language=en-US`
        )
          .then((res) => res.json())
          .then((data) => {
            console.log([...results, { ...data, ...{ media_type: "tv" } }])
            setResults((results) => [
              ...results,
              { ...data, ...{ media_type: "movie" } },
            ])
          })
      } else {
        fetch(
          `https://api.themoviedb.org/3/tv/${snapshot.val()[key].ID}?api_key=${
            process.env.REACT_APP_TMDB_API_KEY
          }&language=en-US`
        )
          .then((res) => res.json())
          .then((data) => {
            console.log([...results, { ...data, ...{ media_type: "tv" } }])
            setResults((results) => [
              ...results,
              { ...data, ...{ media_type: "tv" } },
            ])
          })
      }
    })
  }

  useEffect(() => {
    app
      .database()
      .ref("users")
      .child(`${currentUser.uid}/watchlist/`)
      .once("value", (snapshot) => {
        snapshot.exists() && fetchData(snapshot)
      })
  }, [currentUser.uid])

  return (
    <>
      <NavigationMenu activeItem="watch-list" />

      <Container className="pt-5">
        <div className="row">
          {results
            ? results.map((result) => {
                return (
                  <MediaCard
                    watchList={watchList}
                    key={result.id}
                    result={result}
                    deleteFully={true}
                  />
                )
              })
            : null}
        </div>
      </Container>
    </>
  )
}
